<template>
  <div>
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white">任务推送列表</span>
    </mu-appbar>
    <div class="warningContent">
      <div v-for="(item,index) in taskItem" :key="index" @click="toHandle(item)">
        <van-cell-group>
          <!-- <van-cell title="单元格" value="内容" /> -->
          <van-cell :title="item.station" :value="item.type" :label="'任务编号：'+item.taskId" />
        </van-cell-group>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      taskItem: [],
      user: ""
    };
  },
  mounted() {
    this.user = sessionStorage.getItem("user");
    this.taskItem = this.$store.state.websocketTaskData;
    for (let index = 0; index < this.taskItem.length; index++) {
      var controlName = this.taskItem[index].type;
      switch (controlName) {
        case "mobile":
          this.taskItem[index].type = "保养检修";
          break;
        case "maintenance":
          this.taskItem[index].type = "故障维修";
          break;
        case "inspect":
          this.taskItem[index].type = "例行巡检";
          break;
      }
    }
  },
  methods: {
    toHandle(item) {
      console.log(item);
      if (this.user !== "manager" && this.user !== "station") {
        this.$router.push({
          path: "maintenance",
          query: {
            taskId: item.taskId,
            stationName: item.station,
            type: item.type
          }
        });
      }
    }
  }
};
</script>
<style scoped>
</style>


